<!--张旭 -->
<template>
  <div>
    <el-card class="header">
      <div id="name"
           class="header_item">
        <el-col :span="12">
          <label>雇主姓名：</label>
        </el-col>
        <el-input v-model="seachname"
                  placeholder="请输入雇主姓名"></el-input>
      </div>
      <div id="phone"
           class="header_item">
        <el-col :span="12">
          <label>雇主手机号码：</label>
        </el-col>
        <el-input v-model="seachphone"
                  placeholder="请输入雇主手机号"></el-input>
      </div>
      <div id="name"
           class="header_item">
        <el-col :span="13">
          <label>需求类型：</label>
        </el-col>
        <!-- :disabled="seachtype === 'materialreview'" -->
        <el-select v-model="seachgroup"
                   placeholder="请选择需求类型">
          <el-option v-for="item in headerItemDataAll"
                     :key="item.id"
                     :label="item.title"
                     :value="item.id"></el-option>
        </el-select>
      </div>
      <div id="name"
           class="header_item">
        <el-col :span="13">
          <label>上下架状态：</label>
        </el-col>

        <!-- :disabled="seachtype === 'materialreview'" -->
        <el-select v-model="seachFrame"
                   placeholder="请选择上下架状态">
          <el-option v-for="item in headerItemDataFrame"
                     :key="item.id"
                     :label="item.title"
                     :value="item.id"></el-option>
        </el-select>
      </div>
      <div id="query"
           class="header_item">
        <el-button @click="queryData"
                   type="primary">查询</el-button>
        <el-button type="default"
                   @click="cleanData">清除筛选项</el-button>
      </div>
    </el-card>
    <el-divider></el-divider>
    <el-card>
      <div class="over-table">
        <span class="count">数据统计：共{{adminNumber}}个</span>
        <div class="btn-group">
          <el-button type="primary"
                     @click="addAction">发布需求</el-button>
        </div>
      </div>
      <div id="tableDiv">
        <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple">
              <el-table :data="tableData"
                        border
                        style="width: 100%">
                <el-table-column prop="id"
                                 label="ID"
                                 width="80"
                                 align="center"></el-table-column>
                <el-table-column prop="publisher_name"
                                 label="雇主姓名"
                                 width="180"
                                 align="center"></el-table-column>
                <el-table-column prop="service_type.title"
                                 label="需求类型"
                                 width="130"
                                 align="center"></el-table-column>
                <el-table-column prop="service_salary.salary"
                                 width="100"
                                 label="需求金额"
                                 align="center"></el-table-column>
                <el-table-column type="index"
                                 label="技能要求"
                                 align="center"
                                 width="220">
                  <template slot-scope="scope">
                    <div>
                      <el-tag style="margin: 5px;"
                              v-for="(val, index) in scope.row.service_babel"
                              :key="index">{{val.name}}</el-tag>
                    </div>
                  </template>
                </el-table-column>
                <el-table-column type="index"
                                 label="性格要求"
                                 align="center"
                                 width="220">
                  <template slot-scope="scope">
                    <div>
                      <el-tag style="margin: 5px;"
                              v-for="(val, index) in scope.row.nature"
                              :key="index">{{val.name}}</el-tag>
                    </div>
                  </template>
                </el-table-column>
                <el-table-column label="籍贯要求"
                                 width="220"
                                 align="center">
                  <template slot-scope="scope">
                    <div v-if="scope.row.region.length !== 0">
                      <el-tag style="margin: 5px;"
                              v-for="(val, index) in scope.row.region"
                              :key="index">{{val.name}}</el-tag>
                    </div>
                    <div v-else>
                      <el-tag style="margin: 5px;">不限</el-tag>
                    </div>
                  </template>
                </el-table-column>
                <el-table-column prop="service_age.name"
                                 label="年龄要求"
                                 align="center"></el-table-column>
                <el-table-column prop="experience_id.name"
                                 label="经验要求"
                                 align="center"></el-table-column>
                <el-table-column type="index"
                                 :index="indexRoleSingle"
                                 label="是否成单"
                                 align="center"
                                 width="80"></el-table-column>
                <el-table-column type="index"
                                 :index="indexRoleFrame"
                                 label="上下架状态"
                                 align="center"
                                 width="100"></el-table-column>
                <el-table-column label="操作"
                                 width="100"
                                 align="center">
                  <template slot-scope="scope">
                    <div v-if="scope.row.status === 'release'">
                      <el-button @click="handleShow(scope.row)"
                                 type="text"
                                 size="small">详情</el-button>
                      <el-button @click="handleDel(scope.row)"
                                 type="text"
                                 size="small">下架</el-button>
                    </div>
                    <div v-else-if="scope.row.status === 'complete'">
                      <el-button @click="handleShow(scope.row)"
                                 type="text"
                                 size="small">详情</el-button>
                    </div>
                    <div v-else>
                      <el-button @click="handleShow(scope.row)"
                                 type="text"
                                 size="small">详情</el-button>
                      <el-button @click="handleOpen(scope.row)"
                                 type="text"
                                 size="small">上架</el-button>
                    </div>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="block"
           style="height:30px; text-align: center"
           v-if="tableData.length !== 0">
        <el-pagination background
                       layout="prev, pager, next"
                       :total="adminNumber"
                       prev-text="上一页"
                       next-text="下一页"
                       @current-change="changePage">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>
<script>
import {
  getManagementListService,
  getConditionsService,
  getNeedsStatus
} from '../../../service/Management.js'
import { getLoginInfo } from '../../../assets/common/utils'

export default {
  name: 'demandRecords',
  data () {
    return {
      tableData: [],
      // materialreview:材料审核 fieldcalibration:现场校验, matchcontrol:比赛控制
      updateGroup: 0,
      updateGroupShow: 0,
      updateGroupError: [],
      headerItemData: [],
      headerItemDataAll: [],
      headerItemDataFrame: [{ id: 'all', title: '不限' }, { id: 'soldout', title: '下架' }, { id: 'release', title: '上架' }, { id: 'complete', title: '已成单' }],
      addAdminAction: false,
      addSeccesAction: false,
      formLabelWidth: '160px',
      adminNumber: 0,
      value: false,
      page: 1,
      perPage: 10,
      activeName: 'toAudit',
      radio: '5',
      seachgroup: 'all',
      seachname: '',
      seachphone: '',
      seachFrame: 'all',
      isHiddenAddBtn: true,
      activityId: '',
      groupId: '',
      stop: true,
      addRules: {
      },
      showExport: false
    }
  },
  components: {},
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['需求管理', '需求列表'])
  },
  mounted () {
    this.conditionsAction()
    this.staffListAction(this.page)
  },
  methods: {
    // 导入请求服务
    // 上架下架
    needsStatusAction (id) {
      let params = `?id=${id}`
      getNeedsStatus(params).then(data => {
        this.staffListAction(this.page)
      })
    },
    // 需求条件
    conditionsAction () {
      getConditionsService().then(data => {
        this.headerItemDataAll = [{ id: 'all', title: '不限' }]
        this.headerItemDataAll = this.headerItemDataAll.concat(data.data.service_type_list)
      })
    },
    // 需求列表函数
    staffListAction (page) {
      console.log('列表请求开始')
      var params = `?need_type=housekeep&page=${page}&per_page=${this.perPage}&housework_company_id=${getLoginInfo('company_id')}`
      if (this.seachname !== '') {
        params += `&name=${this.seachname}`
      }
      if (this.seachphone !== '') {
        params += `&phone=${this.seachphone}`
      }
      if (this.seachgroup !== 'all') {
        params += `&service_type_id=${this.seachgroup}`
      }
      if (this.seachFrame !== 'all') {
        params += `&status=${this.seachFrame}`
      }
      getManagementListService(params).then(data => {
        this.tableData = data.data.items
        this.adminNumber = data.data.total
        this.value = this.adminNumber < 11
      })
    },
    indexRoleSingle (index) {
      return this.roleTitleFrame(this.tableData[index].status)
    },
    indexRoleFrame (index) {
      return this.roleTitle(this.tableData[index].status)
    },
    roleTitle (status) {
      switch (status) {
        case 'soldout':
          return '下架'
        case 'release':
          return '上架'
        case 'complete':
          return '下架'
        default:
          return ''
      }
    },
    roleTitleFrame (status) {
      switch (status) {
        case 'soldout':
          return '未成单'
        case 'release':
          return '未成单'
        case 'complete':
          return '已成单'
        default:
          return ''
      }
    },
    handleClick (tab, event) {
      console.log(tab, event)
      console.log(this.group)
      this.page = 1
      this.staffListAction(this.page)
    },
    /// 页数改变
    changePage (index) {
      this.page = index
      this.staffListAction(this.page)
    },
    /// 条件搜索
    queryData () {
      this.page = 1
      this.staffListAction(this.page)
    },
    /// 清除搜索
    cleanData () {
      this.seachtype = 'all'
      this.seachgroup = 'all'
      this.seachname = ''
      this.seachphone = ''
      this.seachFrame = 'all'
      this.page = 1
      this.staffListAction(this.page)
    },
    /// 上架
    handleOpen (row) {
      console.log(row)
      this.needsStatusAction(row.id)
    },
    /// 禁用
    handleShow (row) {
      console.log(row)
      // staffMemberUnshowAction(this, row.id)
      this.$router.push(`/demandDetail/${row.id}`)
    },
    /// 下架
    handleDel (row) {
      console.log(row)
      this.$confirm('是否下架该需求', '提示', {
        confirmButtonText: '是',
        cancelButtonText: '否',
        type: 'warning'
      })
        .then(() => {
          this.needsStatusAction(row.id)
        })
        .catch(() => {
        })
    },
    addAction () {
      this.$router.push('/releaseManagement')
    }
  }
}
</script>
<style scoped>
@import "../../../assets/styles/common.css";
.header {
  /* height: 40px; */
  margin: 10px auto;
  text-align: left;
  overflow: hidden;
  padding: 10px 10px;
}
#addselectnumber {
  text-align: left;
  overflow: hidden;
}
#addothernumber {
  /* height: 40px; */
  /* margin: 10px auto; */
  text-align: left;
  overflow: hidden;
  padding: 10px 0px;
  /* flex: 1; */
}
.header_item {
  width: 100%;
  padding-right: 20px;
  padding-bottom: 5px;
  height: 100%;
  float: left;
  box-sizing: border-box;
  display: flex;
}
#name label,
#phone label {
  line-height: 40px;
  /* padding-right: 10px; */
}
.el-input {
  width: auto;
}
#headerDiv {
  margin-bottom: 15px;
  overflow: hidden;
}
#numLine {
  width: 8px;
  height: 33px;
  background-color: #393a51;
  border-radius: 2x;
  float: left;
}
#addAdmin {
  margin-top: 0px;
  background-color: #ffffff;
  color: #393a51;
  border-radius: 20px;
  border-width: 1px;
  border-color: #393a51;
  float: right;
}
.addCancelBtn {
  background-color: #ffffff;
  color: #393a51;
  border-width: 1px;
  border-color: #393a51;
}
#adminNumLabel {
  color: #999999;
  float: left;
  /* margin-top: 8px; */
  padding: 5px 10px;
  border-radius: 4px;
}
#seccesTitle {
  text-align: center;
  margin: 10px auto;
}
#eucstaff {
  color: red;
  float: left;
  font-size: 30px;
}
.over-table {
  overflow: hidden;
  margin-bottom: 20px;
}
.btn-group {
  float: right;
}
/* .el-table >>> th {
  background-color: #eeeeee;
  text-align: center;
}
.el-table >>> th .cell {
  border-left: #a5a4a4 solid 1px;
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
}
.el-table >>> th:first-child .cell {
  border-left: transparent solid 1px;
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bolder;
  color: #333;
} */
</style>



